* {
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
  }
  
  #search-box {
    font: inherit;
    padding: 8px;
    max-width: 320px;
    margin-bottom: 16px;
  }
  
  .country-list {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  .country-list__element {
    max-width: 500px;
    max-height: 100px;
    overflow: hidden;
    margin: 0 auto;
  
    display: flex;
  }
  
  .country-list__element img {
    width: 80px;
    height: 40px;
  }
  
  .country-info img {
    width: 200px;
    height: auto;
  }
  .bold {
    font-weight: 700;
  }
  